import api from './index.js'

export default {
  async getCategories () {
    const res = api().get('/api/categories')
    return res
  },
  async cerateCategories (data) {
    const res = api().post('/api/categories', data)
    return res
  },
  async updateCategories (data, id) {
    const res = api().put(`/api/categories/${id}`, data)
    return res
  },
}
