import api from './index.js'

export default {
  async getDepartment () {
    const res = api().get('/api/departments')
    return res
  },
  async createDepartment (data) {
    const res = api().post('/api/departments', data)
    return res
  },
  async updateDepartment (data, id) {
    const res = api().put(`/api/departments/${id}`, data)
    return res
  }
}
